@charset "UTF-8";
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}
img{
    vertical-align: top;
}
body{
    font-size: 12px;
    color: #606060;
}
input{
    outline: none;
}
%header-style{
    /*头部logo样式*/
    width: 100%;
        height: 88px;
        padding-top: 8px;
        .logo{
            display: block;
            width: 210px;
            height: 80px;
            padding-left: 10px;
            float: left;
        }
}
@mixin style($w:1000px,$h:auto,$left:0) {
    /*总体内容*/
    width: $w;
    height: $h;
    margin: $left $h;
}
%main-style{
    /*中间内容样式*/
    width: 100%;
    height: 500px;
    padding: 20px 0px 10px 0px;
    position: relative;
}
//包裹表单的div样式
@mixin content-style( $w:380px,$t:50px,$r:34px) {
    width: $w;
    top: $t;
    right: $r;
    position: absolute;
    z-index: 99;
}
//边框样式
// @mixin boder($w:1px,$st:solid,$co:#dcdddd) {
//     border:$w $st $co;
// }
.login{
    //登录界面
    @include style();
    .header{
       @extend %header-style; 
       border-bottom: 3px solid #4bae00;
    }
    .main{
        @extend %main-style;
        position: relative;
        .bg{
            height: 500px;
            background: url(https://passport.womai.com/upload/backend/2020/03/06/16/1583483294392.jpg) no-repeat;
        }
        .content{
            @include content-style();
        }
        form{
            width: 374px;
            padding: 10px 33px;
            border:  1px solid #fff;
            background-color: #f7f8f8;
            margin: 3px auto;
            height: 458px;
            h3{
                height: 20px;
                line-height: 20px;
                font-weight: normal;
                color: #595757;
                font-size: 16px;
                margin-bottom: 20px;
            }
            input{
                width: 302px;
                height: 26px;
                padding: 5px;
                border: #ccc 1px solid;
                font-size: 14px;
            }
            p{
                line-height: 22px;
                margin-top: 2px;
                text-align: right;
            }
            p a{
                color: #aaaaaa;
            }
            p a:hover{
                color: #8cb91e;
            }
            .psword{
                margin-bottom: 26px;
            }
            #login,#registry{
                height: 30px;
                border: none;
                cursor: pointer;
                font-weight: bold;
                font-size: 16px;
                background: url(../img/login.png) no-repeat 0px -5px ;
            }
            #login{
                width: 209px;    
                color: #fff;   
            }
            #registry{
                margin-left: 12px;
                width: 79px;
                color: #479202;
                background-position: -89px -149px;
            }
            .partner{
                margin-top: 22px;
                width: 306px;
                height: auto;
            }
            .partner .line{
                width: 306px;
                height: 0;
                border-top: 1px dashed #aaaaaa;
                margin-bottom: 15px;
            }
            .partner p{
                height: 16px;
                line-height: 16px;
                color: #595757;
                text-align: left;
            }
            .partner ul li{
                height: 20px;
                line-height: 20px;
                margin-top: 16px;
            }
            .partner ul li span{
                margin-right: 10px;
                color: #dcdddd;
            }
            .partner ul li a{
                color:#807e7e;
                padding-left: 18px;
                padding-right: 12px;
                position: relative;
            }
            .partner ul li a:hover{
                color: #8cb91e;
            }
            .partner ul li a i{
                display: inline-block;
                width: 16px;
                height: 20px;
                background: url(../img/login.png) no-repeat -209px -75px;
                position: absolute;
                left: 0;
                top: 0;
            }
            .partner ul li:nth-child(1) a:nth-of-type(2) i{
                background-position: -209px -96px;
            }
            .partner ul li:nth-child(1) a:nth-of-type(3) i{
                background-position: -209px -117px;
            }
            .partner ul li:nth-child(2) a:nth-of-type(1) i{
                background-position: -209px -138px;
            }
            .partner ul li:nth-child(2) a:nth-of-type(2) i{
                background-position: -209px -159px;
            }
            .partner ul li:nth-child(2) a:nth-of-type(3) i{
                background-position: -209px -180px;
            }
            .partner ul li:nth-child(2) a:nth-of-type(4) i{
                background-position: -209px -201px;
            }
            .partner ul li:nth-child(3) a:nth-of-type(1) i{
                background-position: -209px -223px;
            }
            .partner ul li:nth-child(3) a:nth-of-type(2) i{
                background-position: -209px -243px;
            }
        }
        .error{
            display: none;
            position: absolute;
            z-index: 99;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            width: 420px;
            height: 175px;
            background: #fff;
            h2{
                padding: 10px 0 20px 30px;
                color: #333;
                font-size: 20px;
            }
            p{
                width: 380px;
                padding: 0 20px;
                line-height:30px;
                color: #333;
                font-size: 14px;
                text-align: center;
            }
            input{
                width: 420px;
                color: #fff;
                background: #78b54f;
                height: 40px;
                line-height: 40px;
                text-align: center;
                font-size: 14px;
                cursor: pointer;
                margin-top: 20px;
                border: none;
            }
            span{
                position: absolute;
                right: 10px;
                top:10px;
                font-size: 16px;
                cursor: pointer;
            }
        }
    }
}

.footer{
    width: 100%;
    background: #efefef;
    li:nth-child(1){
        padding-top: 20px;
        width: 1000px;
        margin: 0 auto;
        line-height: 26px;
        font-size: 12px;
        text-align: center;
        .beian a{
            color: #606060;
        }
        img{
            margin-top:4px;
        }
        .beian a:hover{
            text-decoration: underline;
        }
    }
    .login-end{
        height: 28px;
        background: #8cb91e;
    }
}
